import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  change,
  selectCount,
} from './colorSlice';
import {Button,Card,Image} from "react-bootstrap"
import "./card.css";
import Register from "../register/registration";
export function Color() {
  const count = useSelector(selectCount);
  const dispatch = useDispatch();
  const [newColor, setNewColor] = useState("");

  return (
    <div>
     
        <div>Theme:</div>
      <div >
        <select  value={newColor} onChange={e => setNewColor(e.target.value)} >
        <option>select</option>
        <option value="dark">Drak</option>
        <option value="light">Light</option>
        </select>
        <Button onClick={() =>
            dispatch(change(newColor))
          }
        >
          Change Theme
        </Button>
       <Card style={{backgroundColor:"powderblue",color:"black"}}>
       <Card.Title>
         <Image className="card" src="dog.gif" />
         <h3>AIM<small>'S</small>:</h3>
        </Card.Title>
         <Card.Body>
         <ul>
         <li>Get to know about your dog.</li>
         <li>Dog training tutorial's for most of the dog breed's.</li>
         <li>Nearby petshop's.</li>
         <li>Foods and toys liked by dog's</li>
         </ul>
         </Card.Body>
         <Card.Footer>
         We only show registered shop's with us. This list will update as more and more people's connect with us
         </Card.Footer>
      </Card>
      </div>
    </div>
  );
}
