import React,{useState,useEffect} from 'react';
import { Color } from './features/color/Color';
import {Container,Navbar,Nav,Form,Button} from "react-bootstrap";
import {useSelector} from "react-redux";
import {selectCount} from "./features/color/colorSlice";
import Register from "./features/register/registration";
import {BrowserRouter as Router ,Switch,Route} from "react-router-dom";
import Shop from "./features/link/shop";
import User from "./features/link/User";
import Help from "./features/link/help";
function App() {
   const [font,setFont]=useState("black")
   const count = useSelector(selectCount);
 
   useEffect(()=>{
   	if (count=="black") {
   		setFont("white")
   	}
   	else{
   		setFont("black")
   	}
   },[count])

  return (
    <div className="App" style={{backgroundColor:count,color:font}}>
    <Container fluid>
    <Router>
    <header>
      <h1>Brisk.Com</h1>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
      <Nav className="mr-auto justify-content-center">
      <Nav.Link href="#menu"><i className="material-icons">menu</i></Nav.Link>
      <Nav.Link href="#profile"><i className="material-icons">perm_identity</i></Nav.Link>
      <Nav.Link href="#downloads"><i className="material-icons">get_app</i></Nav.Link>
      </Nav>
      <Form inline>
      <Form.Control type="text" placeholder="search" className="mr-sm-2"/>
      <Button type="submit" variant="outline-info"><i className="material-icons">search</i></Button>
      </Form>
      </Navbar.Collapse>
      </Navbar>
    </header>
         <Switch>
          <Route exact path="/reduxProject"><Color /></Route>
          <Route exact path="/reduxProject/registershop"><Shop/></Route>
          <Route exact path="/reduxProject/registerasuser"><User/></Route>
          <Route exact path="/reduxProject/help"><Help/></Route>
          </Switch>
          </Router>
   </Container>
    </div>
  );
}

export default App;
